const Transition = ({ children }) => {
  const { ref, inView } = useInView({
    threshold: 1,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        overflow: 'hidden'
      }}
    >
      <Box
        sx={{
          transition: [
            'opacity 1000ms ease-out',
            'transform 1000ms ease-out'
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

